<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
    <div class="wrapper-md">
        <div class="panel panel-default">
            <div class="panel-heading">
                
            </div>
            <div class="wrapper">
                <div class="table-responsive">
                    <table class="table">
                        <tr>
                            <td></td>
                            <td><input type="text" class="form-control" v-model="new_user.username"></td>
                            <td></td>
                            <td><input type="text" class="form-control" v-model="new_user.password"></td>
                            <td></td>
                            <td>
                                <input type="text" class="form-control" v-model="new_user.phone">
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td><input type="text" class="form-control" v-model="new_user.email"></td>
                            <td>
                                <a class="btn btn-default" @click="add()"></a>
                            </td>
                        </tr>
                    </table>
                </div>
            </div>
        </div>
        <div class="panel panel-default">
            <div class="panel-heading">
                
            </div>
            <div class="row wrapper">
                <div class="table-responsive">
                    <table class="table table-hover">
                        <thead>
                        <tr>
                            <th></th>
                            <!--<th></th>-->
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                        </thead>
                        <tbody>
                        <tr  v-for="(key,user) in all_user">
                            <td>
                                <span v-show="edit">{{user.username}}</span>
                                <span v-show="!edit"><input type="text" v-model="user.username"></span>
                            </td>
                          <!--  <td>
                                <span v-show="edit" class="lang">{{user.password}}</span>
                                <span v-show="!edit"><input type="text" v-model="user.password"></span>
                            </td>-->
                            <td>
                                <span v-show="edit">{{user.phone}}</span>
                                <span v-show="!edit"><input type="text" v-model="user.phone"></span>
                            </td>
                            <td>
                                <span v-show="edit">{{user.email}}</span>
                                <span v-show="!edit"><input type="text" v-model="user.email"></span>
                            </td>
                            <td>
                                <span><a @click="del(user.username)"></a></span>
                                <span @click="editFn()">
                        <a v-show="edit"></a>
                    <a @click="update(key)" v-show="!edit"></a>
                    </span>
                            </td>
                        </tr>
                        </tbody>
                    </table>
                    <footer class="panel-footer">
                        <!--  -->
                        <v-nav :cur.sync="cur" :all.sync="totalPage" v-on:btn-click="listen" class="text-center"></v-nav>
                    </footer>
                </div>
            </div>
        </div>


    </div>
</template>
<script>
    import {
            USER_INSERT,
            USER_DELETE,
            FIND_USER_LIST
    } from '../common-path';
    import {GET_COOKIE} from '../js/cookie'
    import vSelect from 'vue-select'
    import vNav from './vue-pagination.vue'
    export default{
        components: {vNav},
        data(){
            return {
                cur: 1,
                totalPage: '',
                all_user: [],
                new_user: {
                    username: "",
                    password: "",
                    email: "",
                    phone: ""
                },
                edit: true,
            }
        },
        watch: {
            //
            cur(val){
                this.GetAllUser(val, 10)
            }
        },
        ready(){
            //
            this.GetAllUser(1, 10)
        },
        methods: {
            //
            listen(data) {
                this.GetAllUser(data, 10);
            },
            //
            GetAllUser(pageNum, pageSize){
                this.$http.post(FIND_USER_LIST, {pageNum: pageNum, pageSzie: pageSize}).then((respones)=> {
                    this.$set('cur', pageNum);
                    this.$set('totalPage', respones.body.data.totalPage);
                    this.$set('all_user', respones.body.data.data);
                })
            },
            //
            del(name){
                this.$http.post(USER_DELETE, {username: name}).then((response)=> {
                    this.GetAllUser(this.cur, 10)
                    response.body.code == 0 ? alert(response.body.error): alert('success!');
                })
            },
            editFn(){
                this.$set('edit', !this.edit);
            },
            update(key){
                this.$http.post(USER_INSERT, this.all_user[key]).then((response)=> {
                    response.body.code == 0 ? alert(response.body.error): alert('success!');
                    this.GetAllUser(this.cur, 10)
                });
            },
            add(){
                this.$http.post(USER_INSERT, this.new_user).then((response)=> {
                   response.body.code == 0 ? alert(response.body.error): alert('success!');
                    this.GetAllUser(this.cur, 10)
                    this.$set('new_user',{
                        username: "",
                        password: "",
                        email: "",
                        phone: ""
                    },)
                });
            }
        }
    }
</script>
<style>
    td > .lang {
        width: 380px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        display: inline-block;
    }
</style>


